body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; }

.home-title {
  text-align: center; }

.showing__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  grid-gap: .5rem;
  padding-bottom: 4rem; }

.showing__cell {
  display: flex;
  flex-direction: column;
  align-items: center; }

.showing__poster {
  width: 110px;
  height: 160px; }

.showing__title {
  padding: 0.5rem 0;
  text-align: center; }

.react-tabs {
  display: flex;
  flex-direction: column; }
  .react-tabs__tab-list {
    position: fixed;
    bottom: 0;
    left: 0;
    padding-left: 0;
    margin: 0 0;
    list-style: none;
    width: 100vw; }
  .react-tabs__tabs-container {
    display: flex;
    justify-content: space-around;
    box-shadow: #000 0 2px 4px 0;
    background-color: #fff; }
  .react-tabs__tab {
    text-align: center;
    color: #989FAE;
    padding: 1rem 0; }
    .react-tabs__tab--selected {
      border-top: 3px solid #ff8200; }



